.wrapper {
  width: 1060px;
  margin: 0 auto;
}

.container {
  display: flex;
  flex-direction: column;
}

.quizContainer {
  width: 1060px;
}

.row,
.budgetRow {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 30px;
}

.col {
  width: 200px;
}

.carouselSlide {
  background-color: #244d4d;
  color: white;
  padding: 10px;
  margin-bottom: 50px;
}

.carouselSlide input {
  box-shadow: none;
}

.secondaryBtn {
  background-color: white;
  border: none;
  color: #244d4d;
  letter-spacing: 1.5px;
  padding: 10px 25px;
  font-weight: 500;
  font-size: 15px;
  margin-top: 15px;
  margin-bottom: 15px;
}

.secondaryBtn:hover {
  background-color: rgb(233, 233, 233);
}

/* BUDGET */

.budgetRow input {
  margin: 0;
  padding: 0;
  height: 0;
  width: 0;
  display: none;
}

.budgetIcon:hover {
  filter: brightness(1) grayscale(0.2) opacity(0.9);
}

.budgetRow input:checked + .budgetIcon {
  border: 2px solid white;
}

.firstTier {
  background: url("./imgs/shoppingbag.png");
}

.secondTier {
  background: url("./imgs/shoppingBag2.png");
}

.thirdTier {
  background: url("./imgs/shoppingBag3.png");
}

.budgetIcon {
  cursor: pointer;
  background-size: contain;
  background-repeat: no-repeat;
  height: 100px;
  width: 100px;
  padding: 15px;
  display: inline-block;
}

/* STYLE SELECTION*/
.bohemian {
  background: url("./imgs/bohemian.jpeg");
}

.farmhouse {
  background: url("./imgs/farmhouse.jpeg");
}

.contemporary {
  background: url("./imgs/contemporary.jpeg");
}

.modern {
  background: url("./imgs/modern.jpeg");
}

.styleCol {
  margin-left: 50px;
}

.styleImg {
  cursor: pointer;
  background-size: contain;
  background-repeat: no-repeat;
  height: 300px;
  width: 500px;
  -webkit-transition: all 100ms ease-in;
  -moz-transition: all 100ms ease-in;
  transition: all 100ms ease-in;
  display: block;
  filter: brightness(1) grayscale(0.6) opacity(0.8);
}

.styleCol input {
  margin: 0;
  padding: 0;
  height: 0;
  width: 0;
  display: none;
}

.styleImg:hover {
  filter: brightness(1) grayscale(0.2) opacity(0.9);
}

.styleCol input:checked + .styleImg {
  filter: none;
}

/* ROOMS */

.roomContainer {
  display: flex;
  justify-content: center;
}

.roomCol {
  width: 300px;
}

.roomIcon {
  cursor: pointer;
  background-size: contain;
  background-repeat: no-repeat;
  height: 100px;
  width: 100px;
  padding: 15px;
  display: inline-block;
}

.roomIcon:hover {
  filter: brightness(1) grayscale(0.6) opacity(0.8);
  border: 2px solid white;
}

.roomCol input:checked + .roomIcon {
  border: 2px solid white;
}

.livingRoom {
  background: url("./imgs/livingRoom.png");
}

.bedroom {
  background: url("./imgs/bedroom.png");
}

.bathroom {
  background: url("./imgs/bathroom.png");
}

.kitchen {
  background: url("./imgs/kitchen.png");
}

.patio {
  background: url("./imgs/patio.png");
}

.roomCol input {
  margin: 0;
  padding: 0;
  height: 0;
  width: 0;
}

/* COLOR SELECTION + HIDDEN DIVS */

.hidden {
  height: 0px;
  width: 0px;
}

.active {
  display: inline-block;
}

.colorRow {
  display: flex;
  justify-content: center;
  margin: 0 auto;
}

.red {
  background: url("./imgs/red.png");
}

.blue {
  background: url("./imgs/blue.png");
}

.black {
  background: url("./imgs/black.png");
}

.white {
  background: url("./imgs/white.png");
}

.yellow {
  background: url("./imgs/yellow.png");
}

.green {
  background: url("./imgs/green.png");
}

.purple {
  background: url("./imgs/purple.png");
}

.orange {
  background: url("./imgs/orange.png");
}

.gray {
  background: url("./imgs/gray.png");
}

.brown {
  background: url("./imgs/brown.png");
}

.colorIcon {
  cursor: pointer;
  background-size: contain;
  background-repeat: no-repeat;
  height: 50px;
  width: 50px;
  padding: 15px;
  display: inline-block;
}

.colorRow input {
  margin: 0;
  padding: 0;
  height: 0;
  width: 0;
}

.colorCol {
  margin-right: 25px;
}

.colorIcon:hover {
  border: 2px solid white;
}

.colorCol input:checked + .colorIcon {
  border: 2px solid white;
}

/* AVOID ROW AND STYLES */
.avoidRow {
  display: flex;
  justify-content: center;
  margin: 0 auto;
}

.avoidRow label {
  margin: 0 auto !important;
}

.avoidCol .avoidIcon {
  background-size: contain;
  background-repeat: no-repeat;
  padding: 15px;
  display: block;
  height: 100px;
  width: 100px;
  margin: 0 auto;
}

.avoidCol input {
  display: none;
}

.avoidCol {
  margin-right: 20px;
}

.anything {background: url("./imgs/anything.png")}
.anything-active {
  background: 
    url("./imgs/green-check.png"), 
    url("./imgs/anything.png");
}

.pillow {background: url("./imgs/pillow.png")}
.pillow-active {
  background:
    url("./imgs/red_x.png"),
    url("./imgs/pillow.png")
    
}

.lamp {background: url("./imgs/lamp.png")}
.lamp-active {
