}

.art {background: url("./imgs/art.png")}
.art-active {
  background: 
    url("./imgs/red_x.png"),
    url("./imgs/art.png")
}

.decor {background: url("./imgs/decor.png")}
.decor-active {
  background: 
    url("./imgs/red_x.png"),
    url("./imgs/decor.png")
}

.linens {background: url("./imgs/linens.png")}
.linens-active {
  background: 
    url("./imgs/red_x.png"),
