.utensils {background: url("./imgs/utensils.png")}
.utensils-active {
  background:
    url("./imgs/red_x.png"),
    url("./imgs/utensils.png")
}

/* MEDIA QUERIES */

@media screen and (max-width: 960px) {
  .carouselSlide {
    margin-bottom: 30px;
  }

  .quizContainer {
    width: 320px;
    margin: 0 auto;
  }

  .row {
    flex-direction: column;
  }

  .budgetIcon {
    height: 50px;
    width: 50px;
    margin: 0 auto;
  }

  .styleCol {
    margin: 0 auto;
  }

  .styleImg {
    height: 200px;
    width: 250px;
    margin: 0 auto;
  }

  .roomIcon {
    height: 55px;
    background-size: contain;
    background-repeat: no-repeat;
    width: 55px;
  }

  .colorRow {
    flex-wrap: wrap;
    margin-left: 10px;
  }

  .avoidCol .avoidIcon {
    height: 50px;
    width: 50px;
  }

  .avoidCol {
    margin-right: 10px;
    width: 100px;
  }
}

@media only screen and (min-device-width: 768px) and (max-device-width: 1124px) {
  .quizContainer {
    width: 600px;
  }

  .row {
    flex-direction: row;
  }

  .styleCol {
    margin: 0;
    padding: 5px;
  }

  .roomCol {
    width: 200px;
  }
}


@media only screen and (min-device-width: 1024px) and (max-device-width: 1366px) and (-webkit-min-device-pixel-ratio: 1.5) {
  .quizContainer {
    width: 800px;
  }

  .styleImg {
    height: 200px;
    width: 310px;
  }

  .styleCol {
    margin: 0;
    padding: 5px;
  }
}

/* GALAXY FOLD */

@media (max-width: 320px) {
  body {
    font-size: .85rem;
  }

  .quizContainer {
    width: 250px;
  }
  .styleImg {
    height: 100px;
    width: 150px;
  }

  .roomIcon {
    height: 45px;
    width: 45px;
  }

  .colorIcon {
    height: 35px;
  }

  .colorCol {
    margin-right: 0px;
  }

  .avoidCol {
    margin-right: 0px;
  }

  .avoidCol .avoidIcon {
    height: 80px;
  }
}
