.wrapper {
  width: 1060px;
  margin: 0 auto;
}

.container {
  display: flex;
  flex-direction: column;
}

.quizContainer {
  width: 1060px;
}

.row,
.budgetRow {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 30px;
}

.col {
  width: 200px;
}

.carouselSlide {
  background-color: #244d4d;
  color: white;
  padding: 10px;
  margin-bottom: 50px;
}

.carouselSlide input {
  box-shadow: none;
}

.secondaryBtn {
  background-color: white;
  border: none;
  color: #244d4d;
  letter-spacing: 1.5px;
  padding: 10px 25px;
  font-weight: 500;
  font-size: 15px;
  margin-top: 15px;
  margin-bottom: 15px;
}

.secondaryBtn:hover {
  background-color: rgb(233, 233, 233);
}

/* BUDGET */

.budgetRow input {
  margin: 0;
  padding: 0;
  height: 0;
  width: 0;
  display: none;
}

.budgetIcon:hover {
  -webkit-filter: brightness(1) grayscale(0.2) opacity(0.9);
          filter: brightness(1) grayscale(0.2) opacity(0.9);
}

.budgetRow input:checked + .budgetIcon {
  border: 2px solid white;
}

.firstTier {
  background: url(data:image/png;base64,);
}

.secondTier {
  background: url(data:image/png;base64,);
}

.thirdTier {
  background: url(data:image/png;base64,);
}

.budgetIcon {
  cursor: pointer;
  background-size: contain;
  background-repeat: no-repeat;
  height: 100px;
  width: 100px;
  padding: 15px;
  display: inline-block;
}

/* STYLE SELECTION*/
.bohemian {
  background: url(/static/media/bohemian.a33a5b65.jpeg);
}

.farmhouse {
  background: url(/static/media/farmhouse.96160c76.jpeg);
}

.contemporary {
  background: url(/static/media/contemporary.80a914c1.jpeg);
}

.modern {
  background: url(/static/media/modern.d202ed87.jpeg);
}

.styleCol {
  margin-left: 50px;
}

.styleImg {
  cursor: pointer;
  background-size: contain;
  background-repeat: no-repeat;
  height: 300px;
  width: 500px;
  transition: all 100ms ease-in;
  display: block;
  -webkit-filter: brightness(1) grayscale(0.6) opacity(0.8);
          filter: brightness(1) grayscale(0.6) opacity(0.8);
}

.styleCol input {
  margin: 0;
  padding: 0;
  height: 0;
  width: 0;
  display: none;
}

.styleImg:hover {
  -webkit-filter: brightness(1) grayscale(0.2) opacity(0.9);
          filter: brightness(1) grayscale(0.2) opacity(0.9);
}

.styleCol input:checked + .styleImg {
  -webkit-filter: none;
          filter: none;
}

